// 
const REGEX_FORMAT = /yy(?:yy)?|([HMmds])\1?/g
//  Date.parse()  yyyy-dd-mm , 
const REGEX_PARSE = /^(\d{4})-?(\d{1,2})-?(\d{0,2})[^0-9]*(\d{1,2})?:?(\d{1,2})?:?(\d{1,2})?.?(\d{1,3})?$/

/**
 * ,  0
 * @param s 
 * @param l 
 * @returns {string}  0
 */
function p (s, ...l) {
  /*
   * 430
   * slice() 
   */
  return ('000' + s).slice(l | 2 * -1)
}

let format = function (date, pattern) {
  let d = date.getDate()
  let y = date.getFullYear()
  let m = date.getMonth()
  let H = date.getHours()
  let M = date.getMinutes()
  let s = date.getSeconds()
  let flags = {
    yy: p(y),
    yyyy: y,
    m: m + 1,
    mm: p(m + 1),
    d: d,
    dd: p(d),
    H: H,
    HH: p(H),
    M: M,
    MM: p(M),
    s: s,
    ss: p(s)
  }
  if (pattern != null) {
    return pattern.replace(REGEX_FORMAT, function (flag) {
      if (flag in flags) {
        return flags[flag]
      } else {
        return flag
      }
    })
  } else {
    return date.getTime()
  }
}

/*  dayjs ,  https://github.com/nomiddlename/date-format */
function parse (date) {
  if (date == null) return new Date()
  if (date instanceof Date) return date
  if (typeof date === 'string' && !/Z$/i.test(date)) {
    const d = date.match(REGEX_PARSE)
    if (d) {
      return new Date(d[1], d[2] - 1, d[3] || 1, d[4] || 0, d[5] || 0, d[6] || 0, d[7] || 0)
    }
  }
  return new Date(date)
}

export default {
  format,
  parse
}
