<template>
  <label
    class="ph-date-picker"
  >
    {{ showDate }}
    <input
      type="date"
      v-model="selectDate"
    >
  </label>
</template>

<script>
import utils from './utils'

/*
  
  <date-picker
      v-model="date"
      value-format="yyyy/mm/dd" // 
      default-value="2022/2/22" // , default-value > model > : 
    />
 */
export default {
  name: 'PhDatePicker',
  props: {
    value: { // 
      type: Date,
      default: ''
    },
    valueFormat: { // 
      type: String,
      default: 'yyyy-mm-dd'
    },
    defaultValue: { // ,  parse ,  String, Date
      type: [Date, String, Number],
      default: undefined
    }
  },
  data () {
    return {
      showDate: '',
      selectDate: '', // 
      date: null //  Date 
    }
  },
  created () {
    // ,  defaultValue > value > 
    let d = this.defaultValue || this.value
    this.date = utils.parse(d === '' ? null : d)
    this.selectDate = utils.format(this.date, 'yyyy/mm/dd')
    this.showDate = utils.format(this.date, this.valueFormat)
    this.$emit('input', this.date)
  },
  watch: {
    selectDate (v) {
      this.date = utils.parse(v === '' ? null : v)
      this.showDate = utils.format(this.date, this.valueFormat)
      this.$emit('input', this.date)
    }
  }
}
</script>

<style lang="stylus">
  .ph-date-picker
    display inline-block
    box-sizing border-box
    width 150px
    height 35px
    line-height @height
    border 1px solid grey
    border-radius 3px
    position relative
    vertical-align middle
    padding 0 5px
    font-size 14px
    white-space: nowrap
    overflow: hidden
    text-overflow: ellipsis
    input
      position absolute
      left -9999px
</style>
