<template>
  <div>
    <h3></h3>
    <h4>1. <code>Vue</code>  <code>&lt;script&gt;</code> </h4>
    <ph-code>&lt;script src="https://cdn.staticfile.org/vue/2.6.7/vue.min.js"&gt;&lt;/script&gt;</ph-code>
    <p> <code>main.js</code></p>
    <ph-code>import { Toast2 } from 'phui'<br/><br/>Vue.use(Toast2);</ph-code>
    <h4>2. <code>Vue</code>  ES Module </h4>
    <ph-code>import&nbsp;Vue&nbsp;from&nbsp;'vue'</ph-code>
    <p> <code>main.js</code></p>
    <ph-code>import { Toast } from&nbsp;'phui'<br/><br/>Vue.use(Toast);</ph-code>
    <h3></h3>
    <show-code>
      <template>
        <ph-toast :message="msg" :show="show"></ph-toast>
        <button @click="toast">Toast</button>
      </template>
      <template v-slot:code>
        <ph-code>&lt;template&gt;<br/>  &lt;div&gt;<br/>    &lt;button @click="toast"&gt;Toast&lt;/button&gt;<br/>  &lt;/div&gt;<br/>&lt;/template&gt;<br/>&lt;script&gt;<br/>export default {<br/>  name: 'TestProgress',<br/>  methods: {<br/>    toast () {<br/>      this.$toast(' Toast ')<br/>    }<br/>  }<br/>}<br/>&lt;/script&gt;</ph-code>
      </template>
    </show-code>
    <h3>API</h3>
    <h4>Methods</h4>
    <h5>this.$toast(message): Void</h5>
    <p> Toast  3000ms </p>
    <ph-table>
      <template>
        <th></th>
        <th></th>
        <th></th>
      </template>
      <template>
        <tr>
          <td>message</td>
          <td>String</td>
          <td></td>
        </tr>
      </template>
    </ph-table>
  </div>
</template>

<script>
import PhToast from '../components/Toast.vue'
export default {
  name: 'Toast',
  components: {
    PhToast
  },
  data () {
    return {
      msg: ' Toast ',
      show: false
    }
  },
  methods: {
    toast () {
      this.show = true
      setTimeout(() => {
        this.show = false
      }, 3000)
    }
  }
}
</script>
