<template>
  <div>
    <h3></h3>
    <ph-code>import { DatePicker } from 'phui'<br/><br/>Vue.use(DatePicker)</ph-code>
    <p> <code>input-type</code> </p>
    <h3></h3>
    <p><code>v-model</code>  <code>Date</code>  <code>default-value</code> </p>
    <show-code>
      <template>
        <ph-date-picker
          v-model="date"
          value-format="yyyy/mm/dd"
          default-value="2022/2/22"
        ></ph-date-picker>
      </template>
      <template v-slot:code>
        <ph-code>&lt;template&gt;<br/>  &lt;ph-date-picker<br/>    v-model="date"<br/>    value-format="yyyy/mm/dd"<br/>    default-value="2022/2/22"<br/>  &gt;&lt;/ph-date-picker&gt;<br/>&lt;/template&gt;<br/><br/>&lt;script&gt;<br/>export default {<br/>  name: 'DatePickerDemo',<br/>  data: function () {<br/>    return {<br/>      date: new Date()<br/>    }<br/>  }<br/>}<br/>&lt;/script&gt;</ph-code>
      </template>
    </show-code>
    <br />
    <div class="note">
      
      <ol>
        <li>1.  <code>input-type</code> </li>
        <li>2.  <code>input-type</code> </li>
        <li>3. </li>
      </ol>
    </div>
    <h3>API</h3>
    <h4>DatePicker Props</h4>
    <div class="table-panel">
      <ph-table class="date-table">
        <template v-slot:head>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </template>
        <template>
          <tr>
            <td>value-format</td>
            <td>String</td>
            <td></td>
            <td>yyyy-mm-dd</td>
            <td>
              yyyy - yy - mm - , , 0m - dd&nbsp;|&nbsp;d - H&nbsp;|&nbsp;HH - [24]M&nbsp;|&nbsp;MM - s&nbsp;|&nbsp;ss - 
            </td>
          </tr>
          <tr>
            <td>default-value</td>
            <td>String|Date|Number</td>
            <td></td>
            <td>-</td>
            <td> <code>v-model</code> <code>default-value</code>&nbsp;&gt;&nbsp;<code>v-model</code>&nbsp;&gt;&nbsp;<code>new Date()</code></td>
          </tr>
        </template>
      </ph-table>
    </div>

  </div>
</template>

<script>
export default {
  name: 'DatePickerView',
  data () {
    return {
      date: new Date()
    }
  }
}
</script>

